function Status($stateParams, $q) {
    var deferred = $q.defer();

    var statuses = [
        {id: 'new', name: ''},
        {id: 'placed', name: ''},
        {id: 'progress', name: ''},
        {id: 'done', name: ''},
        {id: 'cancel', name: ''},
        {id: 'delete', name: ''}
    ];
    deferred.resolve({data:statuses});
    return {
        selectAll: function () {
            return deferred.promise;
        }
    };
}

Status.$inject = ['$stateParams','$q'];

angular.module('entity').service('Status', Status);