States.$inject = ['$q'];

function States($q) {

    var deferred = $q.defer();

    var states = [
        {state: null, name: ''},
        {state: 'specials', name: ''},
        {state: 'contacts', name: ''},
        {state: 'delivery', name: ''},
        {state: 'stocks', name: ''},
        {state: 'notfound', name: '404'},
        {state: 'cart', name: ''},
        {state: 'history', name: ' '}
    ];

    deferred.resolve({data: states});

    return {
        selectAll: function () {
            return deferred.promise;
        }
    };
}


angular.module('entity').service('States', States);